
































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';

type Result = { title: string, total?: number, items: RecordItem[] }[]

@Component({
  components: {Tabs, Chart},
})
export default class Statistics extends Vue {

  mounted(): void {
    const div = (this.$refs.chartWrapper as HTMLDivElement);
    div.scrollLeft = div.scrollWidth;
  }

  tagString(tags: Tag[]): string {
    return tags.length === 0 ? '无' : tags.map(t => t.name).join('，');
  }

  beautify(string: string): string {
    const now = dayjs();
    const day = dayjs(string);
    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天';
    } else if (day.isSame(now, 'year')) {
      return day.format('M月D日');
    } else {
      return day.format('YYYY年M月D日');
    }
  }

  get dateValueList(): { date: string, value: number | undefined }[] {
    const today = new Date();
    const array = [];

    for (let i = 0; i <= 29; i++) {
      const dateString = dayjs(today)
          .subtract(i, 'day').format('YYYY-MM-DD');
      const found = _.find(this.groupedList, {
        title: dateString
      });
      array.push({
        date: dateString, value: found ? found.total : 0
      });
    }
    array.sort((a, b) => { //字符串只能比较大小，不能相减
      if (a.date > b.date) {
        return 1;
      } else if (a.date === b.date) {
        return 0;
      } else {
        return -1;
      }
    });
    return array;
  }

  get chartOptions() {
    const keys = this.dateValueList.map(item => item.date);
    const values = this.dateValueList.map(item => item.value);
    return {
      grid: {
        left: 0,
        right: 0,
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick: {
          alignWithLabel: true, // 横轴刻度和坐标对齐
        },
        axisLine: {
          lineStyle: {color: '#666'}
        },
        axisLabel: {
          formatter: function (value: string) {
            return value.substring(5);
          }
        }
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [{
        data: values,
        type: 'line',
        symbolSize: 12,
        itemStyle: {borderWidth: 1, color: '$color-line'},
        // symbol: 'circle',
      }],
      tooltip: {
        show: true,
        position: 'top',
        formatter: '{c}',
        triggerOn: 'click',
      },
    };
  }

  get recordList(): RecordItem[] {
    return this.$store.state.recordList;
  }

  get groupedList(): Result {

    const {recordList} = this;

    const newList = clone(recordList)
        .filter(r => r.type === this.type)
        .sort((a, b) => dayjs(b.createdTime).valueOf() - dayjs(a.createdTime).valueOf());
    if (newList.length === 0) {return [] as Result; }
    const result: Result = [{title: dayjs(newList[0].createdTime).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdTime), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdTime).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.forEach(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }

  beforeCreate(): void {
    this.$store.commit('fetchRecords');
  }

  type = '-';
  recordTypeList = recordTypeList;
}
